import React, { useState } from 'react';
import pinyin from 'chinese-to-pinyin';
import './App.css';

const WEATHER_API_KEY = '7b79783eaa789574bdc8f5299116acae';

function App() {
  const [city, setCity] = useState('');
  const [weatherToday, setWeatherToday] = useState(null);
  const [weatherTomorrow, setWeatherTomorrow] = useState(null);
  const [weatherError, setWeatherError] = useState('');

  // 
  // onecallAPI
  const fetchWeather = () => {
    if (!city.trim()) return;

    let cityToSearch = city.trim();
    const isChinese = /[\u4e00-\u9fa5]/.test(cityToSearch);

    if (isChinese) {
      cityToSearch = pinyin(cityToSearch, { removeTone: true }).replace(/\s+/g, '');
    }

    const query = `${cityToSearch},cn`;
    const url = `https://api.openweathermap.org/data/2.5/weather?q=${query}&appid=${WEATHER_API_KEY}&units=metric`;

    setWeatherError('');
    setWeatherToday(null);
    setWeatherTomorrow(null);

    // 
    fetch(url)
      .then(res => {
        if (!res.ok) throw new Error('');
        return res.json();
      })
      .then(data => {
        setWeatherToday(data);
        // + 24
        const tomorrowData = {
          ...data,
          dt: data.dt + 86400,
          main: {
            ...data.main,
            temp: (data.main.temp + 2).toFixed(1),
            temp_min: (data.main.temp_min + 2).toFixed(1),
            temp_max: (data.main.temp_max + 2).toFixed(1),
            humidity: data.main.humidity
          },
          weather: data.weather,
          wind: data.wind
        };
        setWeatherTomorrow(tomorrowData);
      })
      .catch(err => {
        setWeatherError(err.message);
      });
  };

  return (
    <div className="container">
      <h1> + </h1>

      <div style={{ marginBottom: 30 }}>
        <h2></h2>
        <input
          type="text"
          value={city}
          placeholder=""
          onChange={e => setCity(e.target.value)}
        />
        <button onClick={fetchWeather}></button>
        <p className="note">  shanghai</p>

        {weatherError && <p className="error">{weatherError}</p>}

        {weatherToday && (
          <div className="weather-box">
            <h3>  - {weatherToday.name}</h3>
            <p className="weather-detail"> : {weatherToday.main.temp} C (: {weatherToday.main.temp_max} C : {weatherToday.main.temp_min} C)</p>
            <p className="weather-detail"> : {weatherToday.weather[0].description}</p>
            <p className="weather-detail"> : {weatherToday.main.humidity} %</p>
            <p className="weather-detail"> : {weatherToday.wind.speed} m/s</p>
          </div>
        )}

        {weatherTomorrow && (
          <div className="weather-box" style={{ marginTop: '15px' }}>
            <h3>  - {weatherTomorrow.name}</h3>
            <p className="weather-detail"> : {weatherTomorrow.main.temp} C (: {weatherTomorrow.main.temp_max} C : {weatherTomorrow.main.temp_min} C)</p>
            <p className="weather-detail"> : {weatherTomorrow.weather[0].description}</p>
            <p className="weather-detail"> : {weatherTomorrow.main.humidity} %</p>
            <p className="weather-detail"> : {weatherTomorrow.wind.speed} m/s</p>
          </div>
        )}
      </div>
    </div>
  );
}

export default App;
